<template lang="pug">
    v-container(fluid)
        v-card
            v-card-text
                p ID: {{ user.id }}
                p UUID: {{ user.uuid }}
                p 昵称: {{ user.nickname }}
                p 邮箱: {{ user.email }}
                p 权限: {{ getUserPermissionText }}
                p 创建日期: {{ new Date(user.createdAt * 1000).toLocaleString() }}
                p 头像ID: {{ user.avatarId }}
</template>

<script>
    import { mapGetters } from 'vuex'
    export default {
        name: 'UserProfile',
        data () {
            return {
            }
        },
        computed: {
            ...mapGetters([
                'getUserPermissionText'
            ]),
            user () {
                return this.$store.state.user;
            }
        },
        methods: {
        }
    }
</script>

